<template>
  <van-row style="height: calc(100% - 50px)">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">

      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">个人中心</p>
      </van-col>
    </van-row>

    <!-- 主体部分 -->
    <van-row class="dt-content">
      <van-row class="dt-infoImg">
        <van-row class="dt-mainInfo">
          <!-- <van-image :src="require('../../../assets/img/头像.png')"
                     class="dt-touxiang"></van-image> -->
          <headPortrait size="large"
                        :username="information.real_name"></headPortrait>
          <van-row>
            <van-col class="dt-apartment">{{apartment}}</van-col>
          </van-row>
        </van-row>

        <van-row class="dt-toSignIn-box">
          <van-row class="dt-toSignIn"
                   @click="toSignIn">
            <van-col>
              <van-image :src="require('../../../assets/img/跳转签到.png')"
                         height="35.5"
                         width="28"
                         class="dt-SignIn"></van-image>
            </van-col>
            <van-col> <span class="dt-toSignInName">签到</span>
            </van-col>
          </van-row>
        </van-row>
      </van-row>
      <!-- 修改个人信息 -->
      <van-row class="dt-info">
        <van-row class="dt-firstLine"
                 @click="myBasicInfo">
          <van-col :span="2">
            <van-image :src="require('../../../assets/img/修改信息.png')"
                       class="dt-Modifypic"></van-image>
          </van-col>
          <van-col :span="20">
            <div class="dt-Modify">个人信息查看</div>
          </van-col>

          <van-col :span="2">
            <van-image :src="require('../../../assets/img/跳转.png')"
                       height="13.5"
                       width="8"
                       class="dt-Modify-enter"></van-image>
          </van-col>
        </van-row>
        <van-cell @click="showPopup"
                  style="padding:0px;">
          <van-row class="dt-firstLine"
                   @click="modifyPwd">
            <van-col :span="2">
              <van-image :src="require('../../../assets/img/修改密码.png')"
                         class="dt-Modifypic"></van-image>
            </van-col>
            <van-col :span="20">
              <div class="dt-Modify">密码修改</div>
            </van-col>
            <van-col :span="2">
              <van-image :src="require('../../../assets/img/跳转.png')"
                         height="13.5"
                         width="8"
                         class="dt-Modify-enter"></van-image>
            </van-col>
          </van-row>
        </van-cell>
        <van-cell v-if="showCheckUpdate"
                  @click="checkUpdate"
                  style="padding:0px;">
          <van-row class="dt-firstLine"
                   @click="modifyPwd">
            <van-col :span="2">
              <van-image :src="require('../../../assets/img/检查更新.png')"
                         class="dt-Modifypic"></van-image>
            </van-col>
            <van-col :span="20">
              <div class="dt-Modify">检查更新</div>
            </van-col>
            <van-col :span="2">
              <van-image :src="require('../../../assets/img/跳转.png')"
                         height="13.5"
                         width="8"
                         class="dt-Modify-enter"></van-image>
            </van-col>
          </van-row>
        </van-cell>
        <van-popup v-model="show"
                   position="bottom"
                   closeable
                   round
                   :style="{ height: '50%' }">
          <van-row style="font-size:20px;margin:20px 0;">修改密码</van-row>
          <van-row>
            <van-cell-group>
              <van-field v-model="old_pwd"
                         size="large"
                         type="password"
                         label="原密码"
                         placeholder="请输入原密码" />
              <van-field v-model="new_pwd"
                         size="large"
                         type="password"
                         label="新密码"
                         placeholder="请输入新密码" />
              <van-field v-model="renew_pwd"
                         type="password"
                         size="large"
                         label="确认密码"
                         placeholder="请确认新密码" />
            </van-cell-group>
          </van-row>
          <van-row class="dt-btn">
            <van-col span="12">
              <van-button plain
                          round
                          type="info"
                          @click="cancelPwd"
                          style="width:100px;">取消</van-button>
            </van-col>
            <van-col span="12">
              <van-button round
                          type="info"
                          @click="confirmPwd"
                          style="width:100px;">确定</van-button>
            </van-col>
          </van-row>
        </van-popup>
      </van-row>
      <!-- 注销登录 -->
      <van-row>
        <div class="dt-button">
          <div class="dt-signOut"
               @click="logOut">注销</div>
        </div>
      </van-row>
    </van-row>

  </van-row>
</template>


<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import headPortrait from "@/components/headPortrait/headPortrait";
import { getDepartment } from "@/utils/user";
import { modifyPassword } from "@/api/userApi";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    headPortrait,
  },
  filters: {
    filtName (name) {
      if (name.length > 2) {
        return name.slice(name.length - 2, name.length)
      } else {
        return name
      }
    }
  },
  data () {
    //这里存放数据
    return {
      // 列表内容
      information: JSON.parse(localStorage.getItem("userInfo")),
      show: false,
      old_pwd: '',
      new_pwd: '',
      renew_pwd: '',
      apartment: '',
      showCheckUpdate: false
    }
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.$store.commit("bottomNav", 3)
    getDepartment(data => {
      this.apartment = data.name
    })
    if (window.android) {
      this.showCheckUpdate = true
    }
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 返回首页
    myBasicInfo () {
      //跳转个人信息界面
      this.$router.push({ name: 'MyBasicInfo' })
    },
    logOut () {
      //注销登录页面
      this.$dialog.confirm({
        message: '是否注销登录',
      }).then(() => {
        /**
         * 人脸识别接口，下个版本部署
         */
        if (window.android) {
          window.android.logout()
          localStorage.removeItem('userInfo')
          localStorage.removeItem('uid')
          localStorage.removeItem('roleInfoList')
        } else if (window.webkit) {
          window.webkit.messageHandlers.logout.postMessage(null)
          localStorage.removeItem('userInfo')
          localStorage.removeItem('uid')
          localStorage.removeItem('roleInfoList')
        } else {
          localStorage.removeItem('userInfo')
          localStorage.removeItem('uid')
          localStorage.removeItem('roleInfoList')
          this.$router.replace({ name: 'Login' })
        }
        // ==========================================
        // localStorage.removeItem('userInfo')
        // this.$router.replace({ name: 'Login' })
      }).catch(() => {
        // on cancel
      });

    },
    toSignIn () {
      //跳转签到页面
      this.$router.push({ name: 'SignIn' })
    },
    showPopup () {
      this.show = true;
    },
    //取消修改密码
    cancelPwd () {
      this.$dialog.confirm({
        message: '是否放弃修改密码',
      }).then(() => {
        this.old_pwd = '',
          this.new_pwd = '',
          this.renew_pwd = '',
          this.show = false;
      }).catch(() => {
        // on cancel
      });
    },
    //确认修改密码
    confirmPwd () {
      if (!this.old_pwd) {
        this.$toast({
          message: '请输入原密码',
          position: 'top',
        });
      } else if (!this.new_pwd) {
        this.$toast({
          message: '请输入新密码',
          position: 'top',
        });
      }
      else if (!this.renew_pwd) {
        this.$toast({
          message: '请输入确认密码',
          position: 'top',
        });
      } else {

        let q = {
          uid: JSON.parse(localStorage.getItem("userInfo")).uid,
          old_pwd: this.old_pwd,
          new_pwd: this.new_pwd,
          renew_pwd: this.renew_pwd,
        }
        modifyPassword(q).then(response => {
          var code = response.code
          console.log(response)
          if (code == 0) {
            this.$toast({
              message: '修改密码成功',
              position: 'top',
            })
            if (window.android) {
              window.android.logout()
              localStorage.removeItem('userInfo')
              localStorage.removeItem('uid')
            } else if (window.webkit) {
              window.webkit.messageHandlers.logout.postMessage(null)
              localStorage.removeItem('userInfo')
              localStorage.removeItem('uid')
            } else {
              localStorage.removeItem('userInfo')
              this.$router.replace({ name: 'Login' })
              localStorage.removeItem('uid')
            }
          } else if (code == 11002) {
            //密码
            this.$toast({
              message: response.msg,
              position: 'top',
            })
          } else if (code == 10002) {
            //密码
            this.$toast({
              message: '两次新密码输入不一致，请重新输入',
              position: 'top',
            })
          } else if (code == 100024) {
            //密码
            this.$toast({
              message: response.msg,
              position: 'top',
            })
          } else if (code == 711025) {
            //密码
            this.$toast({
              message: response.msg,
              position: 'top',
            })
          }

        })
      }
    },
    checkUpdate () {
      window.android.checkUpdate()()
    },
  }
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
//蓝色框部分
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}

.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
}

.dt-content {
  width: 100%;
  height: calc(100% - #{px2rem(100)} - #{$headerHeight} - #{$statusBarHeight});
  background-color: rgb(243, 245, 244);
  background-image: url('../../../assets/img/我的背景图.png');
  background-size: 100% px2rem(216.5);
  padding-top: px2rem(104.5);
  background-repeat: no-repeat;
  .dt-infoImg {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: px2rem(160.5);
    width: 94%;
    margin-left: 3%;
    background-image: url('../../../assets/img/个人中心1.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    .dt-mainInfo {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      .dt-headPortrait {
        height: px2rem(50);
        width: px2rem(50);
      }
      .dt-name {
        color: #333333;
        font-size: px2rem(15);
        margin-top: px2rem(8.5);
        font-weight: 400;
        padding-left: 0;
      }
      .dt-apartment {
        height: px2rem(18);
        width: px2rem(60);
        background-color: #a3ecfa;
        margin-top: px2rem(12);
        font-size: px2rem(11);
        color: #ffffff;
        line-height: px2rem(18);
        font-weight: 400;
        border-radius: px2rem(5);
      }
    }
    .dt-toSignIn-box {
      position: absolute;
      right: 0;
      bottom: 10%;
      width: 100%;
      .dt-toSignIn {
        width: px2rem(109);
        height: px2rem(36);
        background-color: #aebaff;
        float: right;
        margin-top: px2rem(-30);
        border-radius: px2rem(17) 0 0 px2rem(17);
        .dt-SignIn {
          margin-left: px2rem(13.5);
        }
        .dt-toSignInName {
          display: inline-block;
          line-height: px2rem(36);
          font-size: px2rem(14);
          font-weight: 400;
          color: #ffffff;
          margin-left: px2rem(13.5);
        }
      }
    }
  }
}
.dt-info {
  margin-top: px2rem(12);
  width: 94%;
  margin-left: 3%;
  background-color: white;
  border-radius: px2rem(5);
  .dt-firstLine {
    height: px2rem(62);
    .dt-Modifypic {
      padding: px2rem(21) 0 0 px2rem(12);
    }
  }
  .dt-line {
    height: px2rem(0.5);
    background-color: #f0f0f0;
    width: px2rem(326.8);
    margin-left: px2rem(12);
  }
}
.dt-Modify {
  height: px2rem(62);
  text-align: left;
  line-height: px2rem(62);
  padding-left: px2rem(14);
  font-size: px2rem(14);
}
.dt-Modify-enter {
  height: px2rem(62);
  background-color: white;
  padding: px2rem(21) px2rem(14.5) 0 0;
}

.dt-btn {
  height: 20%;
  padding: 5% px2rem(52);
}
.dt-button {
  height: px2rem(40);
  width: 94%;
  margin-left: 3%;
  margin-top: px2rem(12);
  background-color: #5870f8;
  border-radius: px2rem(5);
  //margin: px2rem(21) 0 0 px2rem(12);
  .dt-signOut {
    line-height: px2rem(40);
    font-size: px2rem(16);
    color: #ffffff;
    font-family: 'PingFang-SC-Regular';
    font-size: 400;
    //padding-top: px2rem(12.5);
  }
}
</style>